import React, { useState } from "react";
import { SafeAreaView, useWindowDimensions } from "react-native";
import Header from "../../component/AppBar";
import { Text, TouchableOpacity, View } from "react-native-ui-lib";
import InputComponent from "../../component/InputComponent";
import { SceneMap, TabBar, TabView } from "react-native-tab-view";

const ContractDetailComponent = (props: any) => {
  const { navigation } = props;
  const [index, setIndex] = useState(0);
  const layout = useWindowDimensions();
  const [routes] = useState([
    { key: 'first', title: 'Quy trình' },
    { key: 'second', title: 'Thông tin hồ sơ' },
    { key: 'three', title: 'Lịch sử hồ sơ' },
  ]);
  const renderScene = SceneMap({
    first: () => <View style={{ marginTop: 10, backgroundColor: "#fff", height: "100%" }}><Text>1</Text></View>,
    second: () => <View style={{ marginTop: 10, backgroundColor: "#fff", height: "100%" }}><Text>2</Text></View>,
    three: () => <View style={{ marginTop: 10, backgroundColor: "#fff", height: "100%" }}><Text>3</Text></View>
  });

  // const backToContractScreen = () => {
  //   navigation.replace('Contract')
  // }

  const renderTabBar = (props: any) => {
    return (
      <TabBar
        {...props}
        indicatorStyle={{ backgroundColor: '#62248F' }}
        style={{ backgroundColor: '#FFF' }}
        labelStyle={{ color: 'rgba(0, 32, 77, 0.70)', fontSize: 10, fontWeight: "400", fontFamily: "Inter" }}
        activeColor='#62248F'
      />
    );
  };
  return (
    <View style={{ flex: 1, backgroundColor: "#F8FAFE", borderRadius: 17, position: "relative" }}>
      <Header containerStyle={{
        height: "15%",
      }} backgroundColor='#F2E0FE' color={'#62248F'} label="Hồ sơ của tôi (24)" {...props} />
      <SafeAreaView
        style={{
          backgroundColor: "rgba(255, 255, 255, 1)",
          position: "absolute",
          top: "13%",
          borderTopLeftRadius: 17,
          borderTopRightRadius: 17,
          height: "88%",
          width: "100%"
        }}>
        <TabView
          lazy={({ route }) => route.key === 'second'}
          renderTabBar={renderTabBar}
          navigationState={{ index, routes }}
          renderScene={renderScene}
          onIndexChange={setIndex}
          style={{
            borderTopLeftRadius: 17,
            borderTopRightRadius: 17,
          }}
          sceneContainerStyle={{
            backgroundColor: "#F2F5F8"
          }}
          initialLayout={{ width: layout.width }}
        />
      </SafeAreaView>
    </View>
  )
}

export default ContractDetailComponent;